import { Component, OnInit } from '@angular/core';
declare var GMaps:any;
import { trigger,state, style, animate, transition} from '@angular/animations';

import {Car} from './classes/car';
import {Marker} from './classes/marker';

@Component({
    animations: [
        trigger('fly' , [
            state('void' , style({top : '-100px' , opacity: '0'})),
            state('in' , style({top: '-40px' , opacity: '1'})),
            transition('* => *', [
                animate(500)
            ])
        ])
    ],
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  map:any//----------------------------------------mapvar
  center:any;//------------------------------------map center
  //-
    id:number=0;//----------------------------------person id
    driver_name:string;//---------------------------input
    car_name:string;//------------------------------input
    car_lat:number;//-------------------------------marker lng
    car_lng:number;//-------------------------------marker lat
    form_swch:boolean=false;//--------------------- control to show add driver form
    posInput_swch:boolean=false;//------------------control to position input in form
    markerForm_swch:boolean=false;//----------------control to show add driver form after map click
    selected_driver:number=-1;//-----------------------selected driver for delete
    new_avatar:string='https://image.freepik.com/free-icon/male-user-profile-picture_318-37825.jpg';
  //-
    marker_list:Marker[]=[];
    car_list:Car[]=[];
  constructor(){
    this.center={
      lat:35.6092316,
      lng:51.3384247
    };
    let car_obj:Car= new Car();
    car_obj.avatar='';
    car_obj.car_name='samand';
    car_obj.driver_name='morteza';
    car_obj.id=this.id;
    car_obj.position={
      lat:35.6092316,
      lng:51.3384247
    }
    car_obj.deleted=false;
    car_obj.avatar='https://i.pinimg.com/736x/a5/76/46/a576463a3b5a9e8cb2a22b47c45a6a7f--blonde-boy-haircuts-kids-teen-boy-hairstyles.jpg';
    //-
    this.car_list.push(car_obj)  ;

  }


  ngOnInit(){
   this.map = new GMaps({
      el: '#map-box',
      lat: this.center.lat,
      lng: this.center.lng,
      zoom:10,
      zoomControl : true,
      zoomControlOpt: {
        style : 'SMALL',
        position: 'TOP_LEFT'
      },
      panControl : false,
      streetViewControl : false,
      mapTypeControl: false,
      overviewMapControl: false,
       click:(e)=>this.map_click(e)
    });
  }

  marker_click(car:Car){
      let marker:Marker=new Marker();
      marker.id=0;
      marker.lat=car.position.lat;
      marker.lng=car.position.lng;
      this.marker_list.push(marker);
  }//---------------on marker click handle
  get_marker(car:Car){
      //-
      this.map.addMarker({
          lat: car.position.lat,
          lng:car.position.lng,
          title:car.driver_name,
          click: (e)=>this.marker_click(car)
      });

  }//-----------------add car pos to map in marker
    map_click(event){
        //-
        let marker:Marker=new Marker();
        marker.lat=event.latLng.lat();
        marker.lng=event.latLng.lng();
        //-
        this.car_lat=marker.lat;
        this.car_lng=marker.lng;
    }//-----------handle click on map
    remove_driver(){
       this.car_list.splice(this.selected_driver,1);
       this.selected_driver=-1;
    }//---------------remove driver btn
    add_driver(){
        this.form_swch=true;
    }//---------------add driver btn
    cancel_regist_form(){
        this.form_swch=false;
        this.posInput_swch=false;
        this.car_lat=null;
        this.car_lng=null;
        this.new_avatar='';
        this.car_name='';
        this.driver_name='';
        this.new_avatar='https://image.freepik.com/free-icon/male-user-profile-picture_318-37825.jpg';
    }//--------cancel form
    register_driver(){
        let car_obj:Car= new Car();
        car_obj.avatar=this.new_avatar;
        car_obj.car_name=this.car_name;
        car_obj.driver_name=this.driver_name;
        car_obj.id= ++this.id;
        car_obj.position={
            lat:this.car_lat,
            lng:this.car_lng
        }
        car_obj.deleted=false;
        //-
        this.car_list.push(car_obj);
        //-
        this.cancel_regist_form()
    }//------------register driver
    draw_route(){
        if(this.marker_list.length>0)
        {
            console.log(this.marker_list)
            let n:number= this.marker_list.length;
            this.map.drawRoute({
                origin: [this.marker_list[n-1].lat,this.marker_list[n-1].lng],
                destination: [this.marker_list[(n-1)-1].lat, this.marker_list[(n-1)-1].lng],
                travelMode: 'driving',
                strokeColor: 'blue',
                strokeOpacity: 0.6,
                strokeWeight: 6
            });
        }

    }//--------------------draw route
    get_position(){
        this.form_swch=false;
        this.markerForm_swch=true;
    }//----------------------img click for getting pos

    show_addDriver_after_mapClick(){
        if(this.car_lat !=null)
        {
            this.posInput_swch=true;
            this.add_driver();
            this.markerForm_swch=false;
        }
    }//--------showing add driver form after click on marker in edge
    deleteDriver(index:number){
        for(let i=0;i<this.car_list.length;i++)
        {
            this.car_list[i].deleted=false;
        }
        //
        this.car_list[index].deleted=true;
        this.selected_driver=index;
    }

}



